.tileImage {
	height: auto;
	width: auto;
	max-width: 100%;
	max-height: 200px;
	border: 1px solid #474747;
}

.inactive .tileImage:hover {
	-webkit-box-shadow: 0 0 0 2px #474747;
	        box-shadow: 0 0 0 2px #474747;
	cursor: pointer;
}

@media (hover: none) {
   	.tileImage:hover {
      	-webkit-box-shadow: 0 0 0 0px #474747;
      	        box-shadow: 0 0 0 0px #474747;
	}
}

.tile {
	margin: 10px 0px;
}


